
  ---
title: 
keepAlive: true
---
<template>
  <my-wrapper title="" fit>
    <template v-slot:extra>
      
    </template>

    <my-container>
      <el-steps :active="active" finish-status="success" simple>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <my-form v-if="active===1"
               :model="form"
               :rules="rules1"
               label-width="100px"
               submit-text=""
               :reset-text="null"
               @submit="handleSubmit">
        <my-input name="to" label=""></my-input>
        <my-input name="from" label=""></my-input>
        <my-input name="name" label=""></my-input>
        <my-input name="money" label="">
          <template slot="prepend"></template>
        </my-input>
      </my-form>

      <my-form v-if="active===2"
               label-width="100px"
               :reset-text="null"
               :model="form"
               :rules="rules2"
               @submit="handleSubmit">
        <my-input name="password" label="" :props="{type:'password'}"></my-input>
        <template slot="actions">
          <el-button @click="active=1"></el-button>
        </template>
      </my-form>

      <my-result v-if="active===3"
                 status="success"
                 :model="form"
                 title=""
                 subtitle="">
        <template v-slot:extra>
          <el-button type="primary" @click="restart"></el-button>
          <el-button></el-button>
        </template>
      </my-result>

    </my-container>

  </my-wrapper>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Button as __Button__,Step as __Step__,Steps as __Steps__} from 'element-ui';
     import {MyContainer as __MyContainer__,MyForm as __MyForm__,MyInput as __MyInput__,MyResult as __MyResult__,MyWrapper as __MyWrapper__} from '$ui';
     
     
     
     export default {
    data() {
      return {
        active: 1,
        form: {},
        rules1: {
          to: {required: true},
          from: {required: true},
          name: {required: true},
          money: {required: true}
        },
        rules2: {
          password: {required: true}
        }
      }
    },
    methods: {
      handleSubmit(model) {
        this.form = {
          ...this.form,
          ...model
        }
        ++this.active
      },
      restart() {
        this.form = {}
        this.active = 1
      }
    }
  }
     __vue__.use(__Button__);
__vue__.use(__Step__);
__vue__.use(__Steps__)
     __vue__.use(__MyContainer__);
__vue__.use(__MyForm__);
__vue__.use(__MyInput__);
__vue__.use(__MyResult__);
__vue__.use(__MyWrapper__)
    
  </script>

<style lang="scss" scoped>
  .my-form {
    max-width: 600px;
    margin: 30px auto;
  }
</style>

  